import wx from 'weixin-js-sdk'
import empty from '../components/empty'
import { mapGetters,mapActions } from 'vuex'
import dJq from '../components/djq'
export default {
  name: "my-cards",
  created(){
    if(this.$route.query.token){
      localStorage.setItem('token',decodeURIComponent(this.$route.query.token))
    }
    if(this.$route.query.refreshToken){
      localStorage.setItem('refreshToken',decodeURIComponent(this.$route.query.refreshToken))
    }
    this.getcoupons({data:{status:'D'},this:this,method:this.getcoupons})
  },
  computed:{
    ...mapGetters({
      dajList:'getdaijinquan'
    })
  },
  methods:{
    ...mapActions(['getcoupons']),
    goToIndex(){
      wx.miniProgram.switchTab({
        url: '/pages/home/index'
      })
    },
    gotod(obj){
     // this.$router.push({name:'voutails',query:{id:id,s:0}})
      if(obj.type == 'coupon'){
        // wx.miniProgram.navigateTo({
        //   url:'/pages/webview/index?path=voutails&orderId='+obj.productPromoCodeId+'&csi=0'
        // })
        this.$router.push({name:'voutails',query:{id:obj.productPromoCodeId,csi:0}})
      }
      else if(obj.type == 'ticket'){
        // wx.miniProgram.navigateTo({
        //   url:'/pages/webview/index?path=voutails&csi=0&cs='+ encodeURIComponent(JSON.stringify(obj))
        // })
        this.$router.push({name:'voutails',query:{cs:encodeURIComponent(JSON.stringify(obj)),csi:0}})
      }
    }
  },
  components:{
    dJq ,empty
  }
}



